Meteor.methods({
	resetDemoData: function () {
		var project = {};
		var nPhases;
		var phases = [];
		var phase = {};
		var i;
		ProjectsTouchScreen.remove({});

		phasesHeader = ["phase 1", "phase 2"];

		nPhases = phasesHeader.length;


			phase = {
				phaseName: phasesHeader[0],
				phaseScheduledFinishedDate: moment().format("DD.MM.YYYY"),
			};
			phases.push(phase);
			phaseScheduledFinishedDate = moment().businessAdd(2, 'days');
			phase = {
				phaseName: phasesHeader[1],
				phaseScheduledFinishedDate: moment(phaseScheduledFinishedDate).format("DD.MM.YYYY"),
			};
			phases.push(phase);

		project = {
			projectId: "1459",
			projectName: "projectA",
			phases: phases,
		};
		phases = [];
		ProjectsTouchScreen.insert(project);

		randomDate = Math.floor((Math.random() * 20) + 1) + ".11.2015";
		RKCore.log(randomDate);
		phase = {
			phaseName: phasesHeader[0],
			phaseScheduledFinishedDate: moment(randomDate, "DD.MM.YYYY").format("DD.MM.YYYY"),
		};
		phases.push(phase);
		phaseScheduledFinishedDate = moment(randomDate, "DD.MM.YYYY").businessAdd(2, 'days');
		phase = {
			phaseName: phasesHeader[1],
			phaseScheduledFinishedDate: moment(phaseScheduledFinishedDate).format("DD.MM.YYYY"),
		};
		phases.push(phase);

		project = {
			projectId: "1457",
			projectName: "projectB",
			phases: phases,
		};
		phases = [];
		ProjectsTouchScreen.insert(project);
		return true;
	},
	markPhaseAsDone: function (data) {
		RKCore.log(data);
		return true;
	},
});
